.expl-tabs__thumbs {
	cursor: default;
}

.expl-tabs__thumbs > a {
	user-select: none;
	-webkit-tap-highlight-color: transparent;
}

.expl-tabs__panels {
	z-index: 1;
}

.expl-tabs__panels > div {
	opacity: 0;
	visibility: hidden;
}

.tabs__select-js {
	cursor: pointer;
	user-select: none;
	-webkit-tap-highlight-color: transparent;
}

.expl-tabs__thumbs {
	display: flex;
}

.expl-tabs__thumbs > a {
	padding: 4px 8px;
	margin-right: 2px;
	background-color: lightpink;
	color: white;
}

.expl-tabs__thumbs > a.ms-tabs_active-tab {
	padding: 4px 12px;
	font-weight: bold;
	background-color: lightcoral;
	box-shadow: 0 -3px 0 lightcoral;
}

.tabs__select-js {
	position: relative;
	display: inline-block;
	vertical-align: top;
	cursor: pointer;
	padding: 4px 8px;
	margin-bottom: 10px;
	background-color: lightseagreen;
	color: white;
}

.tabs__select-js > i {
	display: inline-block;
	vertical-align: top;
	margin-left: 5px;
}

.tabs__select-js:active {
	top: 1px;
}

.tabs__select-js.ms-tabs_select-open > i {
	transform: rotate(-180deg);
}

.compact-view-true-js {
	position: relative;
}

@media (min-width: 992px) {
	.compact-view-true-js .tabs__select-js {
		display: none;
	}
}

@media (max-width: 991px) {
	.compact-view-true-js .expl-tabs__thumbs {
		display: none;
		position: absolute;
		left: 0;
		top: 40px;
		width: 240px;
		padding: 10px 8px 5px;
		box-shadow: 0 2px 5px rgba(0, 0, 0, 0.3);
		z-index: 9;
		background-color: lightgoldenrodyellow;
	}
}

@media (max-width: 991px) {
	.compact-view-true-js .expl-tabs__thumbs > a {
		display: block;
		margin: 0 0 5px;
	}
}

@media (max-width: 991px) {
	.compact-view-true-js.ms-tabs_select-open .expl-tabs__thumbs {
		display: block;
	}
}
